import { action, computed, observable, decorate } from "mobx";

export class TodoItem {
  status = "todo";
  name = "";
  constructor(name, id) {
    this.name = name;
    this.id = id;
  }

  start() {
    this.status = "started";
  }

  finish() {
    this.status = "finished";
  }
}

decorate(TodoItem, {
  status: observable,
  name: observable,
  start: action,
  finish: action
});

export class TodoListClass {
  maxId = 0;
  itemById = {};

  get itemIds() {
    return Object.keys(this.itemById).sort();
  }

  get items() {
    return this.itemIds.map(k => this.itemById[k]);
  }

  nextItem(id) {
    const ids = this.itemIds;
    const current = ids.findIndex(i => i === id);
    if (ids.length > current + 1) return this.itemById[ids[current + 1]];
  }

  previousItem(id) {
    const ids = this.itemIds;
    const current = ids.findIndex(i => i === id);
    if (current > 0) return this.itemById[ids[current - 1]];
  }

  remove(id) {
    delete this.itemById[id];
  }

  add(name) {
    this.maxId++;
    this.itemById[this.maxId.toString()] = new TodoItem(
      name,
      this.maxId.toString()
    );
  }
}
decorate(TodoListClass, {
  itemById: observable,
  name: observable,
  remove: action,
  items: computed,
  add: action
});

export const TodoList = new TodoListClass();
TodoList.add("Eat a cheese");
TodoList.add("Sing about sixpence");
TodoList.add("Go forth, or fifth");



