import React, { Component } from "react";
import {
  PhocusContext,
  PhocusButton,
  Action,
  ActionContextService,
  ConstrainFocusService
} from "react-phocus";
import { observer } from "mobx-react";
import ReactDOM from "react-dom";
import "./modal.css";

ActionContextService.addContext("modal", {
  actions: {
    closeModal: new Action({
      name: "Close modal",
      defaultKeys: ["Escape"],
      actOn: component => {
        component.close();
      }
    })
  }
});

export const Modal = observer(
  class Modal extends Component {
    constructor(props) {
      super(props);
      this.el = document.createElement("div");
      document.body.appendChild(this.el);
    }

    close() {
      this.props.close();
    }

    componentWillMount() {
      ConstrainFocusService.pushConstraint(() => this.el);
    }

    componentDidMount() {
      Array.from(document.body.children).forEach(e => {
        if (e === this.el) return;
        if (e.getAttribute('aria-hidden')) e.phocusOldAriaHidden = e.getAttribute('aria-hidden');
        e.setAttribute('aria-hidden', true);
      })
    }

    componentWillUnmount() {
      Array.from(document.body.children).forEach(e => {
        e.setAttribute('aria-hidden', e.dataset.phocusOldAriaHidden || false);
      })
      ConstrainFocusService.popConstraint();
    }

    render() {
      return ReactDOM.createPortal((
        <PhocusContext context="modal" argument={this} className="modal-container">
          <PhocusButton action="closeModal" className="modal-background"><span></span></PhocusButton>
          {this.props.children}
        </PhocusContext>
      ), this.el);
    }
  }
);



