import React, { Component } from "react";
import { PhocusContext, Action, ActionContextService, Hotkey } from "react-phocus";
import ReactDOM from "react-dom";
import "./command-palette.css";
import { Modal } from "./modal";
import { PhocusButton } from "react-phocus/dist/phocus-button/phocus-button";
import { focusInContext } from "phocus/dist/dom-manipulation/focus-in-context";

ActionContextService.addContext("command-palette-available", {
  actions: {
    showPalette: new Action({
      name: "Open command palette",
      defaultKeys: ["Control+p", "Meta+p"],
      actOn: component => {
        component.showPalette();
      }
    })
  }
});

export class WithCommandPalette extends Component {
  state = {
    paletteOpen: false
  };

  showPalette() {
    this.setState({ paletteOpen: true });
  }

  hidePalette = () => {
    this.setState({ paletteOpen: false });
  };

  componentDidMount() {
    ActionContextService.setContext(ReactDOM.findDOMNode(this));
  }

  render() {
    return (
      <PhocusContext
        context="command-palette-available"
        argument={this}
        className="command-palette-context"
      >
        {this.props.children}
        {this.state.paletteOpen && (
          <Modal close={this.hidePalette}>
            <CommandPalette />
          </Modal>
        )}
      </PhocusContext>
    );
  }
}

ActionContextService.addContext("command-palette", {
  actions: {
    doit: new Action({
      name: "Take the first action",
      defaultKeys: ["Enter"],
      actOn: component => {
        ActionContextService.triggerAction("closeModal");
        component.search()[0].act();
      }
    }),
    focusFirst: new Action({
      hidden: true,
      name: "Focus on the first action",
      defaultKeys: ["ArrowDown"],
      actOn: component => {
        focusInContext("action-0")
      }
    }),
  }
});

export class CommandPalette extends Component {
  state = {
    search: ""
  }

  componentWillMount() {
    this.setState({ actions: ActionContextService.availableActions });
  }

  search() {
    return filterActions(this.state.actions, this.state.search);
  }

  render() {
    return (
      <PhocusContext
        context="command-palette"
        argument={this}
        className="command-palette"
      >
        <input onChange={e => this.setState({search: e.target.value})} model={this.state.search} autoFocus />
        {this.search().map((a, i) => (
          <CommandPaletteAction action={a} key={i} id={i} />
        ))}
      </PhocusContext>
    );
  }
}

ActionContextService.addContext("command-palette-action", {
  actions: {
    doit: new Action({
      name: "Take this action",
      defaultKeys: ["Enter"],
      actOn: c => {
        ActionContextService.triggerAction("closeModal");
        c.props.action.act();
      }
    }),
    next: new Action({
      name: "Next",
      defaultKeys: ["ArrowDown"],
      actOn: c => {
        focusInContext(`action-${c.props.id + 1}`)
      }
    }),
    previous: new Action({
      name: "Previous",
      defaultKeys: ["ArrowUp"],
      actOn: c => {
        focusInContext(`action-${c.props.id - 1}`)
      }
    })
  }
});

export class CommandPaletteAction extends Component {
  render() {
    return (
      <PhocusContext
        context="command-palette-action"
        argument={this}
      >
      <PhocusButton
        className="command-palette-action"
        data-phocus-id={`action-${this.props.id}`}
        data-phocus-on-mouseover
        action="doit">
        <div className="name">{this.props.action.action.name}</div>
        <div className="description">{this.props.action.action.shortDocumentation}</div>
        <div className="key-command"><KeyCommand action={this.props.action.action} /></div>
      </PhocusButton>
      </PhocusContext>
    );
  }
}

export class KeyCommand extends Component {
  keys() {
    return this.props.action.keys.filter(k => k !== "None").map(k => Hotkey.displayKey(k));
  }

  render() {
    return (
      <span>
        {this.keys().map((key, i) => {
          return <span key={i}>{!!i && " or "}<span className="key">{key}</span></span>
        })}
      </span>
    );
  }
}

const Bonuses = {
  wordBoundary: 30,
  consecutiveLetters: 15,
  separation: -1,
  distanceFromFilename: -10,
  repeatedSelection: 10,
  recentSelection: 10
}
class Match {
  static search(search, list, key) {
    search = search.toLowerCase();
    const regex = new RegExp(`(.*?)(${search.split("").join(")(.*?)(")})(.*)`);
    let matches = Array.from(list).map((item) =>
      new Match(item, key, regex));
    matches = matches.filter(m => m.matched);
    matches.sort((a, b) => a.score() < b.score() ? -1 : 1)
    matches = matches.reverse();
    return matches.map(m => m.value);
  }

  constructor(value, key, searchRegex) {
    this.value = value;
    this.name = key(value);
    if (!this.name) {
      this.matched = false;
      return;
    }
    this.regex = searchRegex;
    this.matchList = this.name.toLowerCase().match(searchRegex);
    if (!this.matchList) {
      this.matched = false;
      return;
    }
    this.matched = true;
    this.matchList = this.matchList.slice(1);
    this.parts = this.getParts();
  }

  totalLength() {
    return (this.parts[this.parts.length - 1].index - this.parts[0].index) + 1;
  }

  matchedLength() {
    return (this.matchList.length - 1) / 2;
  }

  getParts() {
    let ind = -1;
    const ret = (() => {
      const result = [];
      for (let i = 0, end = this.matchedLength(), asc = 0 <= end; asc ? i < end : i > end; asc ? i++ : i--) {
        ind += this.matchList[i*2].length + this.matchList[(i*2) + 1].length;
        result.push({
          match: this.matchList[(i*2) + 1],
          index: ind
        });
      }
      return result;
    })();
    return ret;
  }

  wordBoundaryBonus() {
    let bonus = 0;
    for (let part of Array.from(this.parts)) {
      if ((part.index === 0) || (this.name[part.index - 1] === "-") || (this.name[part.index - 1] === " ")) {
        bonus += Bonuses.wordBoundary;
      }
    }
    return bonus;
  }

  separationBonus(match) {
    return (this.totalLength() - this.matchedLength()) * Bonuses.separation;
  }

  consecutiveLettersBonus(match) {
    let bonus = 0;
    for (let i = 1, end = this.parts.length, asc = 1 <= end; asc ? i < end : i > end; asc ? i++ : i--) {
      if ((this.parts[i].index - this.parts[i-1].index) === 1) {
        bonus += Bonuses.consecutiveLetters;
      }
    }
    return bonus;
  }

  score() {
    return this.separationBonus() + this.wordBoundaryBonus() + this.consecutiveLettersBonus();
  }
}


function filterActions(actions, search) {
  return Match.search(search, actions, v=>v.action.name)
}


