import React, { Component } from "react";
import {
  PhocusContext,
  PhocusButton,
  Action,
  ActionContextService,
  focusInContext
} from "react-phocus";
import { observer } from "mobx-react";
import { TodoList } from "../todo-list-model";
import "./todo-item-view.css";

ActionContextService.addContext("todo-item", {
  actions: {
    start: new Action({
      name: "Start",
      shortDocumentation: "Marks the task as in progress",
      defaultKeys: ["s"],
      actOn: item => {
        item.start();
      }
    }),
    finish: new Action({
      name: "Finish",
      shortDocumentation: "Marks the task as completed",
      defaultKeys: ["f"],
      actOn: item => {
        item.finish();
      }
    }),
    delete: new Action({
      name: "Delete",
      shortDocumentation: "Removes the task from the list",
      defaultKeys: ["Backspace"],
      actOn: (item, element) => {
        const previous = TodoList.previousItem(item.id);
        const next = TodoList.nextItem(item.id);

        if (previous) {
          focusInContext(`todo-item-${previous.id}`, element);
        } else if (next) {
          focusInContext(`todo-item-${next.id}`, element);
        } else {
          ActionContextService.triggerAction("add");
        }
        TodoList.remove(item.id);
      }
    }),
    next: new Action({
      name: "Next",
      shortDocumentation: "Focuses on the next task",
      defaultKeys: ["ArrowDown", "j"],
      actOn: (item, element) => {
        const next = TodoList.nextItem(item.id);
        next && focusInContext(`todo-item-${next.id}`, element);
      }
    }),
    previous: new Action({
      name: "Previous",
      shortDocumentation: "Focuses on the previous task",
      defaultKeys: ["ArrowUp", "k"],
      actOn: (item, element) => {
        const previous = TodoList.previousItem(item.id);
        previous && focusInContext(`todo-item-${previous.id}`, element);
      }
    })
  }
});

export const TodoItemView = observer(
  class TodoItemView extends Component {
    render() {
      const item = this.props.item;
      return (
        <PhocusContext
          context="todo-item"
          argument={item}
          tag="li"
          tabIndex="0"
          className={`todo-item ${item.status}`}
          data-phocus-id={`todo-item-${item.id}`}
          data-phocus-on-mouseover
        >
          <div className="name">{item.name}</div>
          <div className="buttons">
            {item.status === "todo" && <PhocusButton action="start" />}
            {item.status === "started" && (
              <PhocusButton action="finish" className="success" />
            )}
            <PhocusButton action="delete" className="warn">
              &times;
            </PhocusButton>
          </div>
        </PhocusContext>
      );
    }
  }
);



